:root{
    --background-color: #fff;
    --background-light: #fff;
    --shadow-color: rgba(0,0,0,0.2);
    --primary-color: rgb(255, 0,86);
    --text-color: #0A0A0A;
    --text-light: #575757;
    --font-size: 16px
    --animation-speed: .1s;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


a{
    text-decoration: none;
}

.navbar{
    height: 55px; 
    box-shadow: 0 3px 6px var(--shadow-color);
    background-color: var(--background-color);
    color: var(--text-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
    .logo{
        font-size: 1.2em;
        font-weight: bolder;
        color: var(--primary-color);
        text-shadow: 0 1px 3px var(--shadow-color);
        padding-left: 50px;
    }
       span{
        color: var(--text-color);
       }
       
    


.nav-links a{
        margin-left: 1em;
        color: var(--text-light); 
        letter-spacing: 2px;
        text-transform: uppercase;
        position: relative;
        &::before{
            content:"";
            display: inline-block;
            height: 3px;
            width: 100%;
            background-color: var(--primary-color);
            position: absolute;
            bottom: -5px;
            left: 0;
            transform: scaleX(0);
            transition: transform calc(var(--animation-speed) * 0.3 s) ease-in-out;
        }
        &:hover{
            &::before{
                transform: scaleX(1)
            }
        }
        &.active{
            color:var(--primary-color);
            font-weight: bolder;
        }
}
